import React, { useState, useEffect } from 'react';
import { useMsal } from '@azure/msal-react';
import {
  Box,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Chip,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Alert,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  IconButton,
} from '@mui/material';
import { CheckCircle, Cancel, Visibility } from '@mui/icons-material';
import { approvalApi } from '../services/apiService';

function ApprovalList() {
  const { accounts } = useMsal();
  const [approvalRequests, setApprovalRequests] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [statusFilter, setStatusFilter] = useState('');
  const [selectedRequest, setSelectedRequest] = useState(null);
  const [approvalDialog, setApprovalDialog] = useState(false);
  const [approvalComment, setApprovalComment] = useState('');
  const [processingApproval, setProcessingApproval] = useState(false);

  const currentUser = accounts[0];
  const userRole = currentUser?.idTokenClaims?.roles?.[0] || 'Internal';
  const isManager = userRole === 'Manager' || userRole === 'Internal';

  useEffect(() => {
    loadApprovalRequests();
  }, [statusFilter]);

  const loadApprovalRequests = async () => {
    try {
      setLoading(true);
      const response = await approvalApi.getApprovalRequests(statusFilter);
      setApprovalRequests(response.data);
      setError('');
    } catch (error) {
      console.error(':', error);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  const handleApprovalProcess = async (isApproved) => {
    if (!selectedRequest) return;

    try {
      setProcessingApproval(true);
      await approvalApi.processApproval(
        selectedRequest.id,
        isApproved,
        approvalComment
      );
      
      setApprovalDialog(false);
      setSelectedRequest(null);
      setApprovalComment('');
      await loadApprovalRequests();
    } catch (error) {
      console.error(':', error);
      setError('');
    } finally {
      setProcessingApproval(false);
    }
  };

  const openApprovalDialog = (request) => {
    setSelectedRequest(request);
    setApprovalDialog(true);
    setApprovalComment('');
  };

  const closeApprovalDialog = () => {
    setApprovalDialog(false);
    setSelectedRequest(null);
    setApprovalComment('');
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'Pending': return 'warning';
      case 'Approved': return 'success';
      case 'Rejected': return 'error';
      default: return 'default';
    }
  };

  const getStatusLabel = (status) => {
    switch (status) {
      case 'Pending': return '';
      case 'Approved': return '';
      case 'Rejected': return '';
      default: return status;
    }
  };

  const getRequestTypeLabel = (requestType) => {
    switch (requestType) {
      case 'ProjectCreation': return '';
      case 'ProjectCompletion': return '';
      default: return requestType;
    }
  };

  if (!isManager) {
    return (
      <Box>
        <Alert severity="warning">
          
        </Alert>
      </Box>
    );
  }

  if (loading) {
    return <Typography>...</Typography>;
  }

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">
          
        </Typography>
        <FormControl sx={{ minWidth: 120 }}>
          <InputLabel></InputLabel>
          <Select
            value={statusFilter}
            label=""
            onChange={(e) => setStatusFilter(e.target.value)}
          >
            <MenuItem value=""></MenuItem>
            <MenuItem value="Pending"></MenuItem>
            <MenuItem value="Approved"></MenuItem>
            <MenuItem value="Rejected"></MenuItem>
          </Select>
        </FormControl>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {approvalRequests.length === 0 ? (
              <TableRow>
                <TableCell colSpan={9} align="center">
                  <Typography color="text.secondary">
                    
                  </Typography>
                </TableCell>
              </TableRow>
            ) : (
              approvalRequests.map((request) => (
                <TableRow key={request.id} hover>
                  <TableCell>{getRequestTypeLabel(request.requestType)}</TableCell>
                  <TableCell>{request.partNo}</TableCell>
                  <TableCell>{request.lotNo}</TableCell>
                  <TableCell>{request.requestedByName}</TableCell>
                  <TableCell>
                    {new Date(request.requestedUtc).toLocaleDateString('ja-JP')}
                  </TableCell>
                  <TableCell>
                    <Chip
                      label={getStatusLabel(request.status)}
                      color={getStatusColor(request.status)}
                      size="small"
                    />
                  </TableCell>
                  <TableCell>{request.approvedByName || '-'}</TableCell>
                  <TableCell>
                    {request.approvedUtc 
                      ? new Date(request.approvedUtc).toLocaleDateString('ja-JP')
                      : '-'
                    }
                  </TableCell>
                  <TableCell>
                    <Box sx={{ display: 'flex', gap: 1 }}>
                      {request.status === 'Pending' && (
                        <IconButton
                          size="small"
                          color="primary"
                          onClick={() => openApprovalDialog(request)}
                          title=""
                        >
                          <CheckCircle />
                        </IconButton>
                      )}
                      <IconButton
                        size="small"
                        onClick={() => {/*  */}}
                        title=""
                      >
                        <Visibility />
                      </IconButton>
                    </Box>
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </TableContainer>

      {/*  */}
      <Dialog open={approvalDialog} onClose={closeApprovalDialog} maxWidth="sm" fullWidth>
        <DialogTitle></DialogTitle>
        <DialogContent>
          {selectedRequest && (
            <Box sx={{ mt: 2 }}>
              <Typography variant="h6" gutterBottom>
                {getRequestTypeLabel(selectedRequest.requestType)}
              </Typography>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                : {selectedRequest.partNo} / : {selectedRequest.lotNo}
              </Typography>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                : {selectedRequest.requestedByName}
              </Typography>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                : {selectedRequest.requestComment}
              </Typography>
              
              <TextField
                fullWidth
                multiline
                rows={3}
                label=""
                value={approvalComment}
                onChange={(e) => setApprovalComment(e.target.value)}
                sx={{ mt: 2 }}
                placeholder=""
              />
            </Box>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={closeApprovalDialog}>
            
          </Button>
          <Button
            onClick={() => handleApprovalProcess(false)}
            color="error"
            disabled={processingApproval}
            startIcon={<Cancel />}
          >
            
          </Button>
          <Button
            onClick={() => handleApprovalProcess(true)}
            color="primary"
            disabled={processingApproval}
            startIcon={<CheckCircle />}
          >
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}

export default ApprovalList;
