import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Paper,
  TextField,
  Button,
  Alert,
  Grid,
} from '@mui/material';
import { Save, Cancel } from '@mui/icons-material';
import { projectsApi } from '../services/apiService';

/**
 * 
 * 
 */
function ProjectCreate() {
  const navigate = useNavigate();
  
  // 
  const [formData, setFormData] = useState({
    partNo: '',    // 
    lotNo: '',     // 
    tolUpper: '',  // 
    tolLower: '',  // 
  });
  
  // UI
  const [loading, setLoading] = useState(false);     // 
  const [error, setError] = useState('');           // 
  const [success, setSuccess] = useState('');       // 

  /**
   * 
   * @param {Event} e - 
   */
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  /**
   * 
   * API
   * @param {Event} e - 
   */
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // 
    if (!formData.partNo || !formData.lotNo || !formData.tolUpper || !formData.tolLower) {
      setError('');
      return;
    }

    // 
    const tolUpper = parseFloat(formData.tolUpper);
    const tolLower = parseFloat(formData.tolLower);

    if (isNaN(tolUpper) || isNaN(tolLower)) {
      setError('');
      return;
    }

    //  > 
    if (tolUpper <= tolLower) {
      setError('');
      return;
    }

    try {
      setLoading(true);
      setError('');
      
      // API
      const projectData = {
        partNo: formData.partNo,
        lotNo: formData.lotNo,
        tolUpper: tolUpper,
        tolLower: tolLower,
      };

      // API
      await projectsApi.createProject(projectData);
      setSuccess('');
      
      // 
      setTimeout(() => {
        navigate('/projects');
      }, 2000);
      
    } catch (error) {
      console.error(':', error);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  /**
   * 
   * 
   */
  const handleCancel = () => {
    navigate('/projects');
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        
      </Typography>

      <Paper sx={{ p: 3, maxWidth: 600 }}>
        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}
        
        {success && (
          <Alert severity="success" sx={{ mb: 2 }}>
            {success}
          </Alert>
        )}

        <form onSubmit={handleSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label=""
                name="partNo"
                value={formData.partNo}
                onChange={handleChange}
                required
                disabled={loading}
                placeholder=": PART-001"
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label=""
                name="lotNo"
                value={formData.lotNo}
                onChange={handleChange}
                required
                disabled={loading}
                placeholder=": LOT-2025001"
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label=""
                name="tolUpper"
                type="number"
                value={formData.tolUpper}
                onChange={handleChange}
                required
                disabled={loading}
                inputProps={{ step: 0.001 }}
                placeholder=": 10.5"
              />
            </Grid>
            
            <Grid item xs={12} sm={6}>
              <TextField
                fullWidth
                label=""
                name="tolLower"
                type="number"
                value={formData.tolLower}
                onChange={handleChange}
                required
                disabled={loading}
                inputProps={{ step: 0.001 }}
                placeholder=": 9.5"
              />
            </Grid>
            
            <Grid item xs={12}>
              <Box sx={{ display: 'flex', gap: 2, justifyContent: 'flex-end' }}>
                <Button
                  variant="outlined"
                  startIcon={<Cancel />}
                  onClick={handleCancel}
                  disabled={loading}
                >
                  
                </Button>
                <Button
                  type="submit"
                  variant="contained"
                  startIcon={<Save />}
                  disabled={loading}
                >
                  {loading ? '...' : ''}
                </Button>
              </Box>
            </Grid>
          </Grid>
        </form>
      </Paper>

      <Box sx={{ mt: 3 }}>
        <Typography variant="body2" color="text.secondary">
           CSV
        </Typography>
      </Box>
    </Box>
  );
}

export default ProjectCreate;
