import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography,
  Box,
  Alert,
} from '@mui/material';
import { approvalApi } from '../services/apiService';

function ApprovalRequestDialog({ 
  open, 
  onClose, 
  project, 
  requestType = 'ProjectCreation',
  onSuccess 
}) {
  const [requestComment, setRequestComment] = useState('');
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async () => {
    if (!project || !requestComment.trim()) {
      setError('');
      return;
    }

    try {
      setSubmitting(true);
      setError('');

      await approvalApi.createApprovalRequest({
        projectId: project.id,
        requestType: requestType,
        requestComment: requestComment.trim()
      });

      setRequestComment('');
      onSuccess?.();
      onClose();
    } catch (error) {
      console.error(':', error);
      setError(error.response?.data?.message || '');
    } finally {
      setSubmitting(false);
    }
  };

  const handleClose = () => {
    if (!submitting) {
      setRequestComment('');
      setError('');
      onClose();
    }
  };

  const getRequestTypeLabel = (type) => {
    switch (type) {
      case 'ProjectCreation': return '';
      case 'ProjectCompletion': return '';
      default: return type;
    }
  };

  const getRequestTypeDescription = (type) => {
    switch (type) {
      case 'ProjectCreation': 
        return '';
      case 'ProjectCompletion': 
        return '';
      default: 
        return '';
    }
  };

  return (
    <Dialog open={open} onClose={handleClose} maxWidth="sm" fullWidth>
      <DialogTitle>
        {getRequestTypeLabel(requestType)}
      </DialogTitle>
      <DialogContent>
        {project && (
          <Box sx={{ mt: 2 }}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              : {project.partNo}
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              : {project.lotNo}
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              : {project.tolLower}  {project.tolUpper}
            </Typography>
            
            <Typography variant="body2" sx={{ mt: 2, mb: 2 }}>
              {getRequestTypeDescription(requestType)}
            </Typography>

            {error && (
              <Alert severity="error" sx={{ mb: 2 }}>
                {error}
              </Alert>
            )}

            <TextField
              fullWidth
              multiline
              rows={4}
              label=""
              value={requestComment}
              onChange={(e) => setRequestComment(e.target.value)}
              placeholder=""
              required
              error={!requestComment.trim() && error}
              helperText={!requestComment.trim() && error ? '' : ''}
            />
          </Box>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose} disabled={submitting}>
          
        </Button>
        <Button 
          onClick={handleSubmit} 
          variant="contained" 
          disabled={submitting || !requestComment.trim()}
        >
          {submitting ? '...' : ''}
        </Button>
      </DialogActions>
    </Dialog>
  );
}

export default ApprovalRequestDialog;
