import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useMsal } from '@azure/msal-react';
import { useUserRole } from '../context/UserRoleContext';
import {
  Grid,
  Card,
  CardContent,
  Typography,
  Box,
  Button,
  Paper,
  List,
  ListItem,
  ListItemText,
  Chip,
} from '@mui/material';
import {
  Assignment,
  Assessment,
  CloudUpload,
  TrendingUp,
  CheckCircle,
  Schedule,
} from '@mui/icons-material';
import { projectsApi } from '../services/apiService';

function Dashboard() {
  const navigate = useNavigate();
  const { accounts } = useMsal();
  const { role, isInternal } = useUserRole();
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);

  const currentUser = accounts[0];

  useEffect(() => {
    loadProjects();
  }, []);

  const loadProjects = async () => {
    try {
      const response = await projectsApi.getProjects();
      setProjects(response.data.slice(0, 5)); // 5
    } catch (error) {
      console.error(':', error);
    } finally {
      setLoading(false);
    }
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'Draft': return 'default';
      case 'Locked': return 'warning';
      case 'Fixed': return 'success';
      default: return 'default';
    }
  };

  const getStatusLabel = (status) => {
    switch (status) {
      case 'Draft': return '';
      case 'Locked': return '';
      case 'Fixed': return '';
      default: return status;
    }
  };

  const dashboardCards = [
    {
      title: '',
      description: isInternal ? '' : '',
      icon: <Assignment sx={{ fontSize: 40 }} />,
      color: '#1976d2',
      action: () => navigate('/projects'),
    },
    {
      title: '',
      description: '',
      icon: <Assessment sx={{ fontSize: 40 }} />,
      color: '#388e3c',
      action: () => navigate('/inspections'),
    },
    {
      title: 'CSV',
      description: 'CSV',
      icon: <CloudUpload sx={{ fontSize: 40 }} />,
      color: '#f57c00',
      action: () => navigate('/import'),
    },
  ];

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        
      </Typography>
      
      <Typography variant="h6" color="text.secondary" gutterBottom>
        {currentUser?.name || ''} ({role === 'Internal' ? '' : ''})
      </Typography>

      <Grid container spacing={3} sx={{ mb: 4 }}>
        {dashboardCards.map((card, index) => (
          <Grid item xs={12} md={4} key={index}>
            <Card 
              sx={{ 
                height: '100%', 
                cursor: 'pointer',
                '&:hover': { elevation: 4 }
              }}
              onClick={card.action}
            >
              <CardContent sx={{ textAlign: 'center', p: 3 }}>
                <Box sx={{ color: card.color, mb: 2 }}>
                  {card.icon}
                </Box>
                <Typography variant="h6" gutterBottom>
                  {card.title}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {card.description}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Paper sx={{ p: 3 }}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            {loading ? (
              <Typography>...</Typography>
            ) : projects.length > 0 ? (
              <List>
                {projects.map((project) => (
                  <ListItem key={project.id} divider>
                    <ListItemText
                      primary={`${project.partNo} - ${project.lotNo}`}
                      secondary={`: ${new Date(project.createdUtc).toLocaleDateString('ja-JP')}`}
                    />
                    <Chip
                      label={getStatusLabel(project.status)}
                      color={getStatusColor(project.status)}
                      size="small"
                    />
                  </ListItem>
                ))}
              </List>
            ) : (
              <Typography color="text.secondary">
                
              </Typography>
            )}
            <Box sx={{ mt: 2, textAlign: 'right' }}>
              <Button onClick={() => navigate('/projects')}>
                
              </Button>
            </Box>
          </Paper>
        </Grid>

        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, mb: 2 }}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
              {isInternal && (
                <Button
                  variant="outlined"
                  startIcon={<Assignment />}
                  onClick={() => navigate('/projects/create')}
                  fullWidth
                >
                  
                </Button>
              )}
              <Button
                variant="outlined"
                startIcon={<CloudUpload />}
                onClick={() => navigate('/import')}
                fullWidth
              >
                CSV
              </Button>
              <Button
                variant="outlined"
                startIcon={<Assessment />}
                onClick={() => navigate('/inspections')}
                fullWidth
              >
                
              </Button>
            </Box>
          </Paper>

          <Paper sx={{ p: 3 }}>
            <Typography variant="h6" gutterBottom>
              
            </Typography>
            <Typography variant="body2" color="text.secondary">
              : 1.0.0 (POC)
            </Typography>
            <Typography variant="body2" color="text.secondary">
              : 2025/06/24
            </Typography>
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
}

export default Dashboard;
