import React, { useState } from 'react';
import {
  Box,
  Typography,
  Paper,
  Button,
  Alert,
  LinearProgress,
  Card,
  CardContent,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import {
  CloudUpload,
  CheckCircle,
  Error,
  Info,
  Description,
} from '@mui/icons-material';
import { projectsApi } from '../services/apiService';

function CsvImport() {
  const [selectedFile, setSelectedFile] = useState(null);
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState('info');

  const handleFileSelect = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type !== 'text/csv' && !file.name.endsWith('.csv')) {
        setMessage('CSV');
        setMessageType('error');
        return;
      }
      setSelectedFile(file);
      setMessage('');
    }
  };

  const handleUpload = async () => {
    if (!selectedFile) {
      setMessage('');
      setMessageType('error');
      return;
    }

    try {
      setUploading(true);
      setUploadProgress(0);
      setMessage('...');
      setMessageType('info');

      // ID: {projectId}.csv
      const fileName = selectedFile.name;
      const projectId = fileName.replace('.csv', '');

      // SAS URL
      setUploadProgress(25);
      setMessage('URL...');
      const response = await projectsApi.initializeImport(fileName);
      const { sasUrl } = response.data;

      // Blob Storage
      setUploadProgress(50);
      setMessage('...');
      
      const uploadResponse = await fetch(sasUrl, {
        method: 'PUT',
        body: selectedFile,
        headers: {
          'x-ms-blob-type': 'BlockBlob',
          'Content-Type': 'text/csv',
        },
      });

      if (!uploadResponse.ok) {
        throw new Error('');
      }

      setUploadProgress(100);
      setMessage('CSV');
      setMessageType('success');
      
      // 
      setSelectedFile(null);
      const fileInput = document.getElementById('csv-file-input');
      if (fileInput) {
        fileInput.value = '';
      }

    } catch (error) {
      console.error(':', error);
      setMessage(': ' + error.message);
      setMessageType('error');
    } finally {
      setUploading(false);
      setUploadProgress(0);
    }
  };

  const csvFormatInstructions = [
    {
      icon: <Info color="primary" />,
      primary: '',
      secondary: '{ID}.csv (: 12345678-1234-1234-1234-123456789012.csv)',
    },
    {
      icon: <Description color="primary" />,
      primary: 'CSV',
      secondary: 'SupplierId,Value ()',
    },
    {
      icon: <CheckCircle color="success" />,
      primary: '',
      secondary: 'SUPPLIER001,10.25',
    },
    {
      icon: <Error color="warning" />,
      primary: '',
      secondary: 'Draft',
    },
  ];

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        CSV
      </Typography>

      <Typography variant="body1" color="text.secondary" gutterBottom>
        CSV
      </Typography>

      {/*  */}
      <Paper sx={{ p: 3, mb: 3 }}>
        <Box sx={{ textAlign: 'center' }}>
          <input
            id="csv-file-input"
            type="file"
            accept=".csv"
            onChange={handleFileSelect}
            style={{ display: 'none' }}
            disabled={uploading}
          />
          <label htmlFor="csv-file-input">
            <Button
              variant="outlined"
              component="span"
              startIcon={<CloudUpload />}
              size="large"
              disabled={uploading}
              sx={{ mb: 2 }}
            >
              CSV
            </Button>
          </label>

          {selectedFile && (
            <Box sx={{ mb: 2 }}>
              <Typography variant="body2" color="text.secondary">
                :
              </Typography>
              <Typography variant="body1">
                {selectedFile.name} ({(selectedFile.size / 1024).toFixed(1)} KB)
              </Typography>
            </Box>
          )}

          {uploading && (
            <Box sx={{ mb: 2 }}>
              <LinearProgress variant="determinate" value={uploadProgress} />
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                {uploadProgress}%
              </Typography>
            </Box>
          )}

          <Button
            variant="contained"
            onClick={handleUpload}
            disabled={!selectedFile || uploading}
            size="large"
          >
            {uploading ? '...' : ''}
          </Button>
        </Box>
      </Paper>

      {/*  */}
      {message && (
        <Alert severity={messageType} sx={{ mb: 3 }}>
          {message}
        </Alert>
      )}

      {/* CSV */}
      <Card>
        <CardContent>
          <Typography variant="h6" gutterBottom>
            CSV
          </Typography>
          <List>
            {csvFormatInstructions.map((instruction, index) => (
              <ListItem key={index}>
                <ListItemIcon>
                  {instruction.icon}
                </ListItemIcon>
                <ListItemText
                  primary={instruction.primary}
                  secondary={instruction.secondary}
                />
              </ListItem>
            ))}
          </List>
        </CardContent>
      </Card>

      {/*  */}
      <Paper sx={{ p: 3, mt: 3 }}>
        <Typography variant="h6" gutterBottom>
          
        </Typography>
        <Typography variant="body2" color="text.secondary" paragraph>
          1. CSV
        </Typography>
        <Typography variant="body2" color="text.secondary" paragraph>
          2. Azure Function
        </Typography>
        <Typography variant="body2" color="text.secondary" paragraph>
          3. 
        </Typography>
        <Typography variant="body2" color="text.secondary" paragraph>
          4. 
        </Typography>
        <Typography variant="body2" color="text.secondary">
          5. 
        </Typography>
      </Paper>
    </Box>
  );
}

export default CsvImport;
